<template>
  <div>
    <van-form v-if="dataForm"
              @submit="onSubmit">
      <van-field v-model="dataForm.cooperationName"
                 name="cooperDiscount"
                 :readonly="readonly"
                 input-align="right"
                 label="单位名称"
                 placeholder="请输入单位名称"
                 :rules="[{ required: true, message: '请填写请输入单位名称' }]" />
      <van-field v-model="dataForm.contact"
                 name="contact"
                 :readonly="readonly"
                 input-align="right"
                 label="联系人"
                 placeholder="请输入联系人"
                 :rules="[{ required: true, message: '请填写联系人' }]" />
      <van-field v-model="dataForm.phone"
                 name="phone"
                 :readonly="readonly"
                 input-align="right"
                 label="联系电话"
                 placeholder="请输入联系电话"
                 :rules="[{ required: true, message: '请填写联系电话' },{ validator:validator, message: '请填写正确的联系电话' }]" />
      <van-field v-model="dataForm.email"
                 name="email"
                 :readonly="readonly"
                 input-align="right"
                 label="电子邮箱"
                 placeholder="请输入电子邮箱"
                 :rules="[{ validator:validatorEmail, message: '请填写正确格式的邮箱' }]" />

      <van-field style="margin-top: 12px"
                 v-model="dataForm.accountUsed"
                 input-align="right"
                 readonly
                 label="总消费金额" />
      <van-field label="租房订单"
                 v-model="seemore"
                 readonly
                 input-align="right"
                 right-icon="arrow"
                 @click.native="routerGo('/business-order')" />

      <van-field style="margin-top: 18px"
                 input-align="right"
                 readonly
                 @click.native="routerGo('/business-staff')"
                 right-icon="arrow"
                 label="员工管理" />

      <div style="margin: 16px;">
        <van-button v-if="readonly"
                    @click="readonly = false"
                    native-type="button"
                    block>
          编辑
        </van-button>
        <van-button v-else
                    class="submit-btn"
                    block
                    native-type="submit">
          提交
        </van-button>
      </div>

    </van-form>
  </div>
</template>

<script>
import { GetBusinessCooperation, PostBusinessCooperationEdit } from '@/api/api_business';
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);

export default {
    name: 'details',
    data() {
        return {
            seemore: '查看',
            readonly: true,
            dataForm: null
        };
    },
    created() {
        this.$store.commit('setHeader', this.$route.query.cooperationName);
        this.getDetails();
    },
    methods: {
        //查询协议单位详情
        getDetails() {
            GetBusinessCooperation({ cooperationId: this.$route.query.cooperationId }).done((res) => {
                res.data.accountUsed = res.data.accountUsed || 0;
                this.dataForm = res.data;
            });
        },

        //编辑提交
        onSubmit() {
            PostBusinessCooperationEdit(this.dataForm).done(() => {
                Toast.success('操作成功');
                setTimeout(() => {
                    this.$router.go(-1);
                }, 2000);
            });
        },

        //员工管理
        routerGo(url) {
            this.$router.push({
                path: url,
                query: {
                    cooperationId: this.$route.query.cooperationId,
                    cooperationName: this.$route.query.cooperationName,
                    hotelId: this.$route.query.hotelId
                }
            });
        },

        validator(val) {
            return /^1[3-9][0-9]\d{8}$/.test(val);
        },
        validatorEmail(val) {
            return /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(val);
        }
    }
};
</script>

<style scoped>
.submit-btn {
    background: #ff7d41;
    color: #fff;
}
/deep/ .van-field__label {
    text-align: left;
}
</style>



